<template>
  <div>
    <el-button type="success" @click.native="adddialogVisible = true"></el-button>
    <!--  -->
    <el-dialog title="" :visible.sync="adddialogVisible" width="30%" :before-close="handleClose">
      <el-form ref="add" label-width="100px">
        <el-form-item label="">
          <el-select v-model="addForm.cate_level" @change="selOne" placeholder="">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" v-if="addForm.cate_level != 0">
          <el-select v-model="addForm.one" @change="selTwo" placeholder="">
            <el-option v-for="item in oneList" :key="item.category_id" :label="item.category_name"
              :value="item.category_id">
            </el-option>
          </el-select>
          <el-select v-model="addForm.two" v-if="addForm.cate_level == 2" placeholder="">
            <el-option v-for="item in twoList" :key="item.category_id" :label="item.category_name"
              :value="item.category_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="addForm.cate_name" maxlength="10"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="adddialogVisible = false"> </el-button>
        <el-button type="primary" @click="addCate"> </el-button>
      </span>
    </el-dialog>
    <hr>
    <!--  -->
    <el-table :data="cateList" :row-class-name="tableRowClassName" style="width: 100%;margin-bottom: 20px;"
      row-key="category_id" border :tree-props="{children: 'children', hasChildren: 'hasChildren'}">
      <el-table-column prop="category_name" label="" sortable>
      </el-table-column>
      <el-table-column prop="parent_id" label="id" sortable>
      </el-table-column>
      <el-table-column label="" sortable>
        <template v-slot="scope">
          <div>
            {{scope.row.category_level == 0? "": scope.row.category_level == 1?"":""}}
          </div>
        </template>
      </el-table-column>
      <el-table-column label="" width="180">
        <template v-slot="scope">
          <div>
            <el-button size="mini" type="warning" @click.native="modifyCate(scope.row.category_id)">
            </el-button>
            <!-- 
            <el-popconfirm title="" @onConfirm="delCate(scope.row.category_id, scope.row.category_level)">
              <el-button slot="reference" size="mini" type="danger">
              </el-button>
            </el-popconfirm> -->
          </div>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" width="30%">
      <el-form ref="form" :model="form" :rules="modifyrule" label-width="100px">
        <el-form-item label=":" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetForm('form')"> </el-button>
        <el-button type="primary" @click="submitCate('form')"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        cateList: [],
        form: {
          name: '',
          cateId: 0
        },
        dialogVisible: false,
        modifyrule: {
          name: [{
            required: true,
            message: '',
            trigger: 'blur'
          }]
        },
        // 
        adddialogVisible: false,
        addForm: {
          cate_level: 0,
          one: '',
          two: '',
          cate_name: ''
        },
        oneList: [],
        twoList: [],
        options: [{
            label: '',
            value: 0
          },
          {
            label: '',
            value: 1
          },
          {
            label: '',
            value: 2
          }
        ]
      }
    },
    created() {
      this.getCate()
    },
    methods: {
      // 
      async getCate() {
        let {
          data: result
        } = await this.$http.get("/goods/getCategory")
        console.log(result);
        this.cateList = result.result
      },
      // 
      tableRowClassName({
        row,
        rowIndex
      }) {
        if (row.category_level == 0) {
          return "success-row"
        } else if (row.category_level == 1) {
          return "warning-row"
        }
        return '';
      },
      // 
      async modifyCate(cate_id) {
        console.log(cate_id);
        let {
          data: res
        } = await this.$http.post("/admin/getCateById", {
          cateId: cate_id
        })
        console.log(res);
        if (res.status != 200) return this.$message.error("")
        this.form.name = res.result.category_name
        this.form.cateId = cate_id
        this.dialogVisible = true
      },
      // 
      submitCate(formName) {
        this.$refs[formName].validate(async (valid) => {
          if (valid) {
            let {
              data: res
            } = await this.$http.post("/admin/modifyCateName", {
              form: this.form
            })
            console.log(res);
            if (res.status != 200) return this.$message.error("")
            this.dialogVisible = false
            this.getCate()
            this.$message.success("")
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
        this.dialogVisible = false
      },

      selOne() {
        let temp = this.cateList.filter(item => {
          if (item.category_level == 0) {
            return item
          }
        })
        this.oneList = temp
      },
      selTwo() {
        let temp = this.oneList.filter(item => {
          if (item.category_id == this.addForm.one) {
            return item
          }
        })
        console.log(temp);
        this.twoList = temp[0].children
      },
      async addCate() {
        if (this.addForm.cate_name == '') return this.$message.error("")
        let {
          data: res
        } = await this.$http.post("/admin/addCate", {
          addForm: this.addForm
        })
        console.log(res);
        if (res.status != 200) return this.$message.error('')
        this.getCate()
        this.$message.success("")
        this.adddialogVisible = false
      },
      async handleClose(done) {
        this.addForm = {
            cate_level: 0,
            one: '',
            two: '',
            cate_name: ''
          },
          done();
      },
      delCate(id, cate_level) {
        // console.log(id, cate_level);
        // let level = cate_level == 0? '':cate_level == 1? '': ''
        // this.$confirm(`${level}, ?`, '', {
        //   confirmButtonText: '',
        //   cancelButtonText: '',
        //   type: 'warning'
        // }).then(() => {
        //   this.$message({
        //     type: 'success',
        //     message: '!'
        //   });
        // }).catch(() => {
        //   this.$message({
        //     type: 'info',
        //     message: ''
        //   });          
        // });
      }
    }
  }

</script>
<style>
  .el-table .warning-row {
    background: oldlace;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }

</style>



