<template>
  <div class="sc">
    <div v-if="$route.path == '/user/securityCentre'">
      <!--  -->
      <div class="base_info">
        <div class="head"></div>
        <div class="info">
          <div>
            <label for=""></label>
            <span>{{userInfo.nick_name}}</span>
          </div>
          <div>
            <label for=""></label>
            <span v-if="userInfo.customer_email != null">{{userInfo.customer_email}}</span>
            <span v-else></span>
          </div>
          <div>
            <label for=""></label>
            <span>{{userInfo.mobile_phone | formatPhone}}</span>
          </div>
        </div>
      </div>
      <!--  -->
      <div class="safe">
        <div class="head"></div>
        <div>
          <ul class="safe_ul">
            <li>
              <div class="state">
                <div class="ready" v-if="userInfo.id_card_no != null"></div>
                <div class="noready" v-else></div>
              </div>
              <div class="title">
                
              </div>
              <div class="desc">
                <div>
                  
                </div>
              </div>
              <div class="opt">
                <el-link v-if="userInfo.id_card_no == null" @click.native="switchPage('/user/userIDcheck')"></el-link>
                <el-link v-else @click.native="switchPage('/user/securityCentre/userIDcheck')"></el-link>
              </div>
            </li>
            <li>
              <div class="state">
                <div class="ready" v-if="userInfo.customer_email != null"></div>
                <div class="noready" v-else></div>
              </div>
              <div class="title">
                
              </div>
              <div class="desc">
                <div>
                  
                </div>
              </div>
              <div class="opt">
                <el-link v-if="userInfo.customer_email == null" @click.native="switchPage('/user/bindEmail')">
                </el-link>
                <el-link v-else @click.native="switchPage('/user/securityCentre/bindEmail')"></el-link>
              </div>
            </li>
            <li>
              <div class="state">
                <div class="ready"></div>
                <!-- <div class="noready"></div> -->
              </div>
              <div class="title">
                
              </div>
              <div class="desc">
                <div>
                  
                  6
                </div>
              </div>
              <div class="opt">
                <el-link @click.native="switchPage('/user/securityCentre/modifyPassword')"></el-link>
              </div>
            </li>
            <li>
              <div class="state">
                <div class="ready"></div>
                <!-- <div class="noready"></div> -->
              </div>
              <div class="title">
                
              </div>
              <div class="desc">
                <div>
                  
                </div>
              </div>
              <div class="opt">
                <el-link @click.native="switchPage('/user/securityCentre/bindPhone')"></el-link>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <!--  -->
    <div>
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
  import {
    mapState
  } from 'vuex'
  export default {
    data() {
      return {

      }
    },
    methods: {
      switchPage(path) {
        this.$router.push(path)
      }
    },
    computed: {
      ...mapState(['userInfo'])
    },
  }

</script>
<style scoped>
  .sc {
    padding: 30px;
  }

  .base_info {
    margin-bottom: 20px;
    border-bottom: 1px solid #ccc;
  }

  .head {
    font-weight: bold;
  }

  .info {
    padding: 10px 0 0 20px;
    display: flex;
    flex-flow: column;
  }

  .info label {
    display: inline-block;
    width: 100px;
    height: 25px;
  }

  .info span {
    display: inline-block;
    width: 150px;
  }

  /*  */

  .safe_ul li {
    height: 60px;
    display: flex;
    margin-top: 20px;
  }

  .state {
    flex: 1;
    font-size: 14px;
  }

  .state>div {
    height: 30px;

    background: url('../../assets/images/msg_bg.png') no-repeat;
  }

  .state .ready {
    padding-top: 20px;
    background-position: 15px -250px;
  }

  .state .noready {
    padding-top: 20px;
    background-position: 15px -200px;
  }

  .title {
    flex: 1;
    font-size: 12px;
  }

  .desc {
    flex: 3.5;
    font-size: 10px;
  }

  .opt {
    flex: 1.5;
    padding-left: 10px;
  }

</style>



