export default{
    // token
    setToken(state, token){
        state.token = token
    },
    // token
    clearToken(state){
        state.token = ''
        state.userInfo = ''
        state.userAddr = ''
        state.address = ''
    },
    // 
    setUserInfo(state, userInfo){
        state.userInfo = userInfo
    },
    // 
    setUserAddr(state, userAddr){
        state.address = userAddr
    },
    // 
    setOrder(state, orderList){
        state.orderList = orderList
    }

}


